#display {
    border: 1px solid black;
    background: rgb(202, 192, 160, 0.5);
    font-size: large;
    text-align: end;
}
#four {
  font-family: 'Open Sans', sans-serif;
  font-family: 'Lato', sans-serif;
}

h1 {
  margin-top: 0px;
}

.Calculator {
  width: 100%;
  height: 100vh;
}

.calculatorBody {
  height: 45%;
  width: 35%;
  border: 1px solid black;
  padding: 1%;
  position: absolute;
  top: 55%;
  left: 50%;
  /* margin-right: -50%; */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 4px 4px;
}

.calculatorBody > :nth-child(1) {
  grid-column: 1 / 5;
}

.calculatorBody > :nth-child(2) {
  grid-column: 1 / 3;
}

.calculatorBody > :nth-child(17) {
  grid-area: 5 / 4 / 7 / 5;
}

.calculatorBody > :nth-child(3) {
  grid-area: 6 / 1 / 7 / 3;
}

button {
  color: #000000 !important;
  font-size: large;
  text-transform: uppercase;
  background: rgb(202, 192, 160, 0.5);
  border: 3px solid rgb(123, 120, 104) !important;
  border-radius: 6px;
  display: inline-block;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  
}

button:hover {
  color: black !important;
  border-radius: 15px;
  border-color: #494949 !important;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  background: rgb(52, 72, 71, 0.5);
  pointer-events: auto;
}

.operation {
  background: rgb(52, 72, 71, 0.3);
}
